<script>
module.exports = {
	name: "BodyMap",
	data: function() {
		return {
			selected: '',
			bodyRegions: ['head', 'waist', 'torso', 'feet', 'legs', ''],
			data: []
		};
	},
	mounted() {
		this.$root.$once('data-is-loaded', (d) => {
			this.data = d;
			// eslint-disable-next-line no-console
			// console.log("got data");
		})
	},
	methods: {
		changeBodySelection: function(area) {
			this.selected = area;
			this.$root.$emit('body-area-selected', area);
		},
		dataForBodyArea: function(area) {
			return this.data.filter(element => (element.placement == area)); 
		},
		getRndInteger: function(min, max) {
			return Math.floor(Math.random() * (max - min) ) + min;
		},
		randomStyle: function (width, height) {
			return { transform: 'translate(' + this.getRndInteger(0,width) + 'px,' + this.getRndInteger(0,height) +'px)'}
		},
	},
	computed: {
		style () {
			return { transform: 'translate(' + this.getRndInteger(-10,40) + 'px,' + this.getRndInteger(-20,40) +'px)'}
		}
  }
};
</script>

<style scoped>
	#bodyMap {
		width: 20%;
	}
	#bodyoutline {
		fill:none; 
		stroke: #384c6c;
		stroke-width: 8px;
	}
	.outline { 
		fill:none; 
		stroke: #384c6c;
		stroke-width: 1px;
	}
	.background {
		fill: #f5f6f8;
	}

	g.selected path.background { fill: green; }
	g:hover path.background { fill: blue; }

	.bodyItemSelector {
		fill: #A9CE38;
		stroke: #384c6c;
		stroke-width: 2px;
	}
</style>